import {Howl} from 'howler';
export class SoundManager {
  bg: Howl;
  constructor() {
    this.bg = new Howl({
      src: ['./sound/bg-sound.mp3'],
      loop: true,
      volume: 0.1
    });
  }

  stopBg() {
    if(this.bg) { 
      this.bg.stop();
    }
  }

  startBg() {
    if(this.bg) {
      this.bg.play();
    }
  }

}
